<template>
    <div class="login-form">
        <p class="login-title">登录</p>
        <div class="login-form-body">
            <el-row>
                <el-col :span="24">
                    <el-form size="medium" :model="sendData" status-icon :rules="rules" ref="Form"  :hide-required-asterisk="true" class="right-form" @submit.native.prevent>
                        <el-form-item class="user-label" prop="account">
                            <div class="user-input">
                              <el-input type="text" v-model="sendData.account" placeholder="请输入账号" auto-complete="off"></el-input>
                            </div>
                        </el-form-item>
                        <el-form-item class="user-label" prop="password">
                            <div class="user-input">
                              <el-input type="password" v-model="sendData.password" placeholder="请输入密码" auto-complete="off"></el-input>
                            </div>
                        </el-form-item>
                        <el-form-item class="btn-label">
                            <el-button  class="login-btn" @click="submitForm('Form')" :loading="sendData.loading">登录</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
// import { userLogin, getUserInfo, } from '@/axios';
export default {
    name: 'LoginForm',
    data(){
        return {
            ischoice:false,//是否保存密码
            sendData: {
                loading:false,
                account:'admin',
                password:'admin',
            },
             rules: {
                account: [
                    { required: true, message: '请输入管理员账号', trigger: 'blur' },
                ],

                password: [
                    { required: true, message: '请输入管理员密码', trigger: 'blur' },
                ],

            }

        }
    },
    methods:{
        submitForm (formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                   this.$router.push(`/admin`);
                    //发送登录请求
                    // const res = await userLogin(this.sendData);
                    //  console.log(res);
                    //   console.log(res.data);


                    // if (res && res.data) {
                    //   //保存登录token

                    //   window.localStorage.setItem(`tokenOwner`, res.data);
                    //   this.$store.dispatch('getUserToken', res.data);

                    //   const user = await getUserInfo();
                    //   console.log(user.data);

                    //   if (user && user.data) {
                    //       window.localStorage.setItem(`currentUserSessionData`, JSON.stringify(user.data));
                    //   } else {
                    //       return this.$message.error(`未获取到您的信息,您无权登陆系统,请与管理员联系!`);
                    //   }


                    //   // 插入动画loading动画
                    //   document.getElementById('app').insertAdjacentHTML('afterend', `<div class="lds-css ng-scope">
                    //       <div class="lds-double-ring">
                    //           <div></div>
                    //           <div></div>
                    //           <div class="loading-text">加载中,请稍后...</div>
                    //       </div>
                    //   </div>`);
                    //   //给个提示语
                    //   this.$message.success(`登陆成功!请稍后!`);
                    //   // 登陆成功了
                    //   this.$router.push(`/own/OwnManagement/OwnInfo`);
                    // }
                } else {
                    this.$message.error('请输入用户名和密码');
                    return false;
                }
            });
        },

        /**
         * 点击记住密码
         */
        choiceClick(){
          console.log(this.ischoice);
          this.ischoice = !this.ischoice;
        }

    }

};
</script>
<style lang="less">
.login-form{
  box-sizing: border-box;
  width:374px;
  height:320px;
  background:rgba(255,255,255,1);
  border:1px solid rgba(238,238,238,1);
  border-radius:2px;
  .login-title{
      width: 100%;
      height: 31px;
      text-align: center;
      font-size:24px;
      font-weight:bold;
      color:rgba(51,51,51,1);
      padding: 45px 0 0 0;
  }
  .login-form-body {
    padding: 20px 0 0 0;
    .right-form {
        width: 300px;

        margin: 0 auto;
        .user-label {
          margin-bottom: 20px;
          height: 40px;
          .el-input /deep/ .el-form-item__content{
             height: 40px;
          }
          .el-input /deep/ .el-input__inner {
              height: 40px;
              line-height: 40px;
          }
          .validate-img{
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
          }
        }
        .user-label:nth-child(2){
           margin-bottom: 0px;
        }
        .btn-label {
          width:300px;
          margin-top:30px;
          box-sizing: border-box;
          /deep/ .el-form-item__content{
             height:44px;
          }
          /deep/ .el-button{
             height:44px;
          }
          .login-btn {
            display: block;
            background:rgba(54,123,246,1);
            font-family: 'PingFang-SC-Medium';
            width: 100%;
            font-size:14px;
            font-weight:500;
            color:rgba(255,255,255,1);
          }
          .login-btn:hover{
            opacity: 0.8;
          }
        }
    }
  }

}
</style>



