<template>
    <div class="login-container">
      <div class="login-header">
        <img class="login-logo" :src="require(`@/assets/imgs/logo_login.png`)" alt="logo">
        <i class="line"></i>
        <div class="login-title">雷震子照明设备管理系统</div>
      </div>
      <div class="login-section" :style="{backgroundImage:`url(${require(`@/assets/imgs/login-bg.png`)})`}">
        <div class="section-form">
            <login-form/>
        </div>
      </div>
      <div class="login-footer">
        Copyright © 2019 <i class="color">雷震子照明设备管理系统</i> - Powered By <i class="color">四川雷震子光电科技有限公司</i>
      </div>
    </div>
</template>
<script type="text/ecmascript-6">
import LoginForm from './form/LoginForm.vue';
export default {
    name: 'login',
    components:{
        LoginForm
    },
};
</script>
<style lang="less" scoped>
.login-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .login-header{
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    background:rgba(255,255,255,1);
    padding: 0 0 0 18.75%;
    .line{
      width:2px;
      height:48px;
      margin: 0 15px 0 15px;
      background:rgba(218,218,218,1);
    }
    .login-title{
      height: 34px;
      font-size:34px;
      font-family:'PingFang-SC-Medium';
      font-weight:bold;
      color:rgba(48,44,41,1);
    }
  }
  .login-section{
    flex: 1;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100% 100%;
    background-clip:border-box;
    display: flex;
    justify-content: center;
    align-items: center;
  }




  .login-footer{
    width: 100%;
    height: 120px;
    background:rgba(255,255,255,1);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size:12px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    .color{
      display: inline-block;
      color:rgba(54,123,246,1);
      margin: 0 5px;
    }
  }
}
</style>




